.app {
  color: #363f41;
  width: 100%;
  height: 100%;
}
.app header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
}
.app header .item {
  flex-grow: 1;
  flex-shrink: 1;
  margin: 0.1rem;
}
.app header .item.fixed {
  flex-grow: 0;
  flex-shrink: 0;
}
.app header .item:not(.fixed) {
  width: 100%;
  max-width: 400px;
}
.app main {
  width: 100%;
  height: 100%;
}
.app.dark {
  color: white;
  background-color: #282c35;
}

.source_select_container {
  width: 100%;
}

.source_select {
  width: 100%;
}