.container {
  display: inline-flex;
  align-items: center;
  width: 100%;
}
.container button {
  background: transparent;
  border: 0;
  width: 38px;
  height: 38px;
  cursor: pointer;
  margin: 0;
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  color: #cccccc;
}
.container button:focus {
  outline: none;
}
.container button.left {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border: 1px solid #cccccc;
  border-right: 0;
}
.container button.right {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border: 1px solid #cccccc;
  border-left: 0;
}
.container input {
  width: 100%;
  height: 38px;
  border: 1px solid #cccccc;
  margin: 0;
  box-sizing: border-box;
  padding: 2px 0.5rem;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.container input.not_empty {
  border-right: 0;
  border-radius: 0;
}
.container input:focus {
  outline: none;
}
.container.dark button {
  color: white;
}
.container.dark input {
  color: white;
  background-color: #282c35;
}